import { useState } from "react";
import { useGetShortMyList } from "queries/ComicUpload/Short/short.query";
import * as S from "./style";
import register from "assets/img/Upload/register.svg";
import { useNavigate } from "react-router-dom";

const ShorToonItem = () => {
  const [page] = useState(1);
  const { data: getShortList } = useGetShortMyList(page, { suspense: true });
  const navigate = useNavigate();

  return (
    <S.ShortContainer>
      <S.ShortWrap>
        <S.ShortListBox>
          <S.ShortListContainer>
            <S.TitleComponent>
              {getShortList && getShortList.shortsResponse.length > 0 ? (
                getShortList.shortsResponse.map((short) => {
                  return (
                    <S.ShortBox
                      onClick={() => {
                        sessionStorage.setItem("ShortId", `${short.id}`);
                        navigate(`/short/manage/${short.id}`);
                      }}
                    >
                      <S.ShortImg alt="thumbnail" src={short.thumbnail} />
                      <S.ShortInfoBox>
                        <S.ShortTitle>{short.title}</S.ShortTitle>
                        <S.GenreBox>
                          {short.genre.map((key) => (
                            <S.ShortGenre>{key}</S.ShortGenre>
                          ))}
                        </S.GenreBox>
                      </S.ShortInfoBox>
                    </S.ShortBox>
                  );
                })
              ) : (
                <S.ShortAddBox onClick={() => {}}>
                  <img alt="shortadd" src={register} />
                  <S.ShortAddTextTop>새 작품 등록</S.ShortAddTextTop>
                  <S.ShortAddTextBottom>
                    새로운 작품을 등록해보세요.
                  </S.ShortAddTextBottom>
                </S.ShortAddBox>
              )}
            </S.TitleComponent>
          </S.ShortListContainer>
        </S.ShortListBox>
      </S.ShortWrap>
    </S.ShortContainer>
  );
};

export default ShorToonItem;
